.product-card {
  width: 200px;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  background-color: $header-background;
  transition-duration: 300ms;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &__header {
    margin-top: 40px;

    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $main-text-color;

    &:hover {
      color: $product-card-header-hover;
    }
  }

  &__code {
    margin-top: 4px;
    margin-bottom: 16px;

    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $code-priceheader-text-color;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 24px;
  }

  &__review {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $main-text-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-header {
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
      color: $code-priceheader-text-color;
    }

    &-sum {
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
      color: $main-text-color;
    }
  }

  &__button {
    display: table-cell;
    vertical-align: middle;

    text-decoration: none;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-align: center;
    color: $header-background;
    text-transform: uppercase;

    border-radius: 5px;
    background-color: $button-background-color;
    width: 166px;
    height: 40px;

    &:hover {
      background: $header-background;
      border: 1px solid $button-background-color;
      color: $button-background-color;
    }
  }
}
